import { Payload, SicBoBetNo } from '@ikigaians/house'

import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { useCartStore } from '@repo/ui/store/useCartStore'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { DesktopModalType, MenuPageType, useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { getVolume, sounds } from '@repo/utils/common/sound'

import { sleep } from '../common/common'
import { BetState } from '../types/betstate'

declare global {
  interface Window {
    chat: {
      isBanned: (isBanned?: boolean) => void
      isTableClosed: (isTableClosed?: boolean) => void
    }
    sound: {
      mute: (sound: string[]) => void
      unMute: (sound: string[]) => void
    }
    winAnim: {
      runWinAnim: (tableDealPayload: Payload) => void
    }
    betState: {
      setBetState: (state: BetState) => void
    }
    layout: {
      setMenuPage: (page: MenuPageType) => void
      desktopModal: (modal: DesktopModalType) => void
    }
    numGrow: {
      setAmount: (small: number, big: number) => void
      setCount: (small: number, big: number) => void
    }
    maintenance: {
      startTime: (timestamp: number) => void
    }
  }
}

// Static flag to track if we've already registered the dev/QA tools
let hasRegistered = false
const getHasRegistered = () => hasRegistered
const setHasRegistered = (value: boolean) => {
  hasRegistered = value
}

export default function useDevQATests() {
  // Register dev/QA tools only in development or QA environments
  const isDev = import.meta.env.VITE_DEPLOY_ENV === 'dev'
  const isCit = import.meta.env.VITE_DEPLOY_ENV === 'cit'
  const isQat = import.meta.env.VITE_DEPLOY_ENV === 'qat'
  const shouldHaveDevTests = isDev || isCit || isQat

  if (!shouldHaveDevTests || getHasRegistered()) return

  // Only register once
  setHasRegistered(true)

  // * Do not delete, used in my .spec files
  window.betState = {
    setBetState: (state: BetState) => {
      useBettingStore.setState({ gameState: state, isBet: state === BetState.Bet })
    },
  }

  window.sound = {
    mute: (name: string[]) => {
      name.forEach((soundName) => {
        const sound = sounds.get(`sound_${soundName}`)
        if (sound) sound.volume(0)
      })
    },
    unMute: (name: string[]) => {
      name.forEach((soundName) => {
        const sound = sounds.get(`sound_${soundName}`)
        if (sound) sound.volume(getVolume(`sound_${soundName}`))
      })
    },
  }
  window.winAnim = {
    // * For Playwright tests
    runWinAnim: async (tableDealPayload: Payload) => {
      // Set betState to settlement to tilt the board
      window.betState.setBetState(BetState.Settlement)

      // Wait for the board to tilt
      await sleep(2000)

      // Trigger the win animation
      useWinAnimStore.getState().onTableDeal(tableDealPayload)
      useWinAnimStore.getState().onTableFinished(tableDealPayload)
    },
  }
  // * For Playwright tests
  window.layout = {
    setMenuPage: (page: MenuPageType) => {
      useLayoutStore.setState({ menuPage: page })
    },
    desktopModal: (modal: DesktopModalType) => {
      useLayoutStore.setState({ desktopModal: modal, prevDesktopModal: null })
    },
  }
  window.numGrow = {
    setAmount: (small: number, big: number) => {
      useCartStore.setState((state) => ({
        betStats: { amount: { [SicBoBetNo.SMALL]: small, [SicBoBetNo.BIG]: big }, count: state.betStats.count },
      }))
    },
    setCount: (small: number, big: number) => {
      useCartStore.setState((state) => ({
        betStats: { amount: state.betStats.amount, count: { [SicBoBetNo.SMALL]: small, [SicBoBetNo.BIG]: big } },
      }))
    },
  }
  window.maintenance = {
    startTime: (timestamp: number) => {
      useConfigStore.setState({ maintenanceStartTime: timestamp })
    },
  }
  console.debug('Dev/QA tools registered')
}
