import { COUNT_DOWN_DELAY_SEC } from '@repo/ui/constants/animation'
import initInterpolationFunc, { InterpolationMode } from '@repo/ui/pixi-react/utils/initInterpolationFunc'
import { useBettingStore } from '@repo/ui/store/useBettingStore'
import { LoadingStageEnum, useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useTimerStore } from '@repo/ui/store/useTimerStore'
import { getWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import { useEffect, useRef } from 'react'
import { LIGHT, TRAFFIC_LIGHT_COLOR, TrafficLightColor } from './TrafficLight'

const linear = initInterpolationFunc(InterpolationMode.LINEAR)
const useWinAnimStore = getWinAnimStore<string>()

export default function useTrafficLightRender(lightRefs: React.RefObject<Map<TrafficLightColor, HTMLDivElement>>) {
  const winStartMs = useRef(0)

  useEffect(() => {
    const renderFrame = (currentMs: number) => {
      const { loadingStage } = useLoadingStore.getState()
      const isLoadingScreen = loadingStage >= LoadingStageEnum.Loaded && loadingStage < LoadingStageEnum.PostLoaded4

      if (isLoadingScreen) {
        lightRefs.current?.forEach((element, color) => {
          const isNormal = color === TRAFFIC_LIGHT_COLOR.NORMAL
          const alpha = isNormal ? 1 : 0
          element.style.opacity = alpha.toString()
        })
        return
      }

      const { betEnd } = useBettingStore.getState()
      const elapsed = (currentMs - (betEnd - 20000)) * 0.001 - COUNT_DOWN_DELAY_SEC

      LIGHT.forEach(({ color, keyFrames }) => {
        const ele = lightRefs.current?.get(color)

        if (!ele || color === TRAFFIC_LIGHT_COLOR.WIN) return

        const alpha = linear(elapsed, keyFrames)
        ele.style.opacity = alpha.toString()
      })

      /* -------------------------------------------------------------------------- */
      /*                             Win frame animation                            */
      /* -------------------------------------------------------------------------- */

      const { animState, winResult } = useWinAnimStore.getState()
      const winEle = lightRefs.current?.get(TRAFFIC_LIGHT_COLOR.WIN)
      const winKeyFrames = LIGHT.find((c) => c.color === TRAFFIC_LIGHT_COLOR.WIN)?.keyFrames

      if (!winEle || !winKeyFrames) return
      if (!animState || !winResult?.result || typeof winResult?.userWin !== 'number') {
        winEle.style.opacity = '0'
        winStartMs.current = 0
        return
      }

      if (winStartMs.current === 0) winStartMs.current = Date.now()

      const isWin = winResult.userWin > 0
      const winElapsed = (currentMs - winStartMs.current) * 0.001
      const winAlpha = isWin ? linear(winElapsed, winKeyFrames) : 0
      winEle.style.opacity = winAlpha.toString()
    }

    const unsubscribe = useTimerStore.subscribe((state) => state.currentMs, renderFrame)
    return () => unsubscribe()
  }, [lightRefs])
}
