import { RefObject, useEffect, useRef, useState } from 'react'

interface UseKeyPressProps {
  targetKey: string
  targetRef?: RefObject<HTMLDivElement | null>
  callback?: (event: KeyboardEvent) => void
}

const useKeyPress = (props: UseKeyPressProps) => {
  const { targetKey, targetRef, callback } = props
  const [isKeyPressed, setIsKeyPressed] = useState(false)

  const callbackRef = useRef(callback)
  useEffect(() => {
    callbackRef.current = callback
  })

  useEffect(() => {
    const targetElement = targetRef?.current ?? window

    const handleKeyDown = (event: Event) => {
      const keyboardEvent = event as KeyboardEvent
      if (keyboardEvent.key === targetKey) {
        setIsKeyPressed(true)
        callbackRef.current?.(keyboardEvent)
      }
    }

    const handleKeyUp = (event: Event) => {
      const keyboardEvent = event as KeyboardEvent
      if (keyboardEvent.key === targetKey) {
        setIsKeyPressed(false)
      }
    }

    targetElement.addEventListener('keydown', handleKeyDown)
    targetElement.addEventListener('keyup', handleKeyUp)

    return () => {
      targetElement.removeEventListener('keydown', handleKeyDown)
      targetElement.removeEventListener('keyup', handleKeyUp)
    }
  }, [targetKey, targetRef])

  return isKeyPressed
}

export default useKeyPress
