import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { isInIframe } from '@repo/ui/utils/common'
import { setMute } from '@repo/utils/common/sound'
import { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import CenterModal from './CenterModal'

export default function ExpiredModal() {
  const isExpired = useNetworkStore((state) => state.isExpired)
  const { t } = useTranslation()
  const LOBBY_URL = useConfigStore((s) => s.LOBBY_URL)
  const canShowExit = isInIframe() || !!LOBBY_URL
  const exitGame = useConfigStore((s) => s.exitGame)

  useEffect(() => {
    if (isExpired) return setMute(true)
  }, [isExpired])

  return (
    <CenterModal
      open={isExpired}
      icon={'expired'}
      text={t('COMMON.SESSION_EXPIRED')}
      confirmText={canShowExit ? t('COMMON.CLOSE') : undefined}
      onConfirm={exitGame}
    />
  )
}
