import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { isInIframe } from '@repo/ui/utils/common'
import { setMute } from '@repo/utils/common/sound'
import { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import CenterModal from './CenterModal'

export default function MultiSessionModal() {
  const isMultiSession = useNetworkStore((state) => state.isMultiSession)
  const { t } = useTranslation()
  const LOBBY_URL = useConfigStore((s) => s.LOBBY_URL)
  const canShowExit = isInIframe() || !!LOBBY_URL
  const exitGame = useConfigStore((s) => s.exitGame)

  useEffect(() => {
    if (isMultiSession) return setMute(true)
  }, [isMultiSession])

  return (
    <CenterModal
      open={isMultiSession}
      icon={'multiSession'}
      text={t('COMMON.REJOIN_TIP')}
      textClassName="w-[10.625rem]"
      confirmText={t('COMMON.REFRESH')}
      onConfirm={() => setTimeout(() => location.reload(), 200)}
      cancelText={canShowExit ? t('COMMON.CLOSE') : undefined}
      onCancel={exitGame}
    />
  )
}
