import { isMobileDevice } from '@repo/utils/common/common'
import { useWindowSize } from '@uidotdev/usehooks'
import { useCallback, useEffect } from 'react'

const HEADER_HEIGHT_DEBOUNCE_DELAY = 150

export default function useGlobalOverlayHeightCalculator() {
  const windowSize = useWindowSize()

  const calculateHeaderHeight = useCallback(() => {
    const headerElement =
      document.getElementById('DesktopHeader') || // Desktop header ID
      document.getElementById('Header') // Mobile main header ID

    let height = 64 // default fallback
    if (headerElement) {
      height = headerElement.getBoundingClientRect().height
    } else {
      height = isMobileDevice ? 48 : 64
    }

    document.documentElement.style.setProperty('--header-height', `${height}px`)
  }, [])

  useEffect(() => {
    const timeoutId = window.setTimeout(calculateHeaderHeight, HEADER_HEIGHT_DEBOUNCE_DELAY)

    return () => clearTimeout(timeoutId)
  }, [windowSize, calculateHeaderHeight])
}
