import { useEffect } from 'react'

import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { isSafari } from '@repo/utils/common/common'

export default function useSafariCheck() {
  const isLoadingFinished = useLoadingStore((s) => s.isLoadingFinished)

  useEffect(() => {
    if (!isLoadingFinished) return

    const isShowSafariHint = isSafari() && !localStorage.getItem('safariLaunch')
    if (isShowSafariHint) {
      useLayoutStore.setState({ menuPage: 'safari_hint' })
    }
  }, [isLoadingFinished])
}
