import { TrafficLightColor } from '@repo/ui/components/trafficLight/TrafficLight'
import { getMainBoard, getMainBoardParams } from '@repo/ui/pixi-react/utils/mainBoardUtils'
import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { useLoadingStore } from '@repo/ui/store/useLoadingStore'
import { useEffect } from 'react'

/**
 * Design uses a mask of dimensions that are bigger 2.875rem in width and 2.375rem in height than the actual frame,
 * We need to make the wrapper bigger than the actual frame to fit the mask
 */
const W_OFFSET = 2.875
const H_OFFSET = 2.375
const EXTEND = 0.0625

export default function useTrafficLightResize(
  wrapperRef: React.RefObject<HTMLDivElement | null>,
  mainBoardRef: React.RefObject<HTMLDivElement | null>,
  lightRefs: React.RefObject<Map<TrafficLightColor, HTMLDivElement>>
) {
  const loadingStage = useLoadingStore((s) => s.loadingStage)

  useEffect(() => {
    let timeoutId: ReturnType<typeof setTimeout>
    let rafId: number
    const resize = () => {
      const params = getMainBoardParams(mainBoardRef)
      if (!params || !mainBoardRef.current || !wrapperRef.current) return

      const ratio = useLayoutStore.getState().rootEmFontSize
      const { width, height, top: mainBoardTop } = params
      // Get the left from the freshest main board ref, left from getMainBoardParams is mainly for pixi
      const { left: mainBoardLeft } = mainBoardRef.current.getBoundingClientRect()
      const { top: wrapperTop, left: wrapperLeft } = wrapperRef.current.getBoundingClientRect()
      const top = mainBoardTop - wrapperTop
      const left = mainBoardLeft - wrapperLeft

      const newStyles = {
        left: `calc(${left - (W_OFFSET * ratio) / 2}px)`,
        top: `calc(${top - (H_OFFSET * ratio) / 2}px - ${EXTEND / 1.5}rem)`,
        width: `calc(${width}px + ${W_OFFSET}rem)`,
        height: `calc(${height}px + ${H_OFFSET}rem + ${EXTEND}rem)`,
      }

      lightRefs.current.forEach((element) => Object.assign(element.style, newStyles))
    }

    getMainBoard(mainBoardRef)

    if (!wrapperRef.current || !mainBoardRef.current) return

    const debounceResize = () => {
      if (timeoutId) clearTimeout(timeoutId)
      timeoutId = setTimeout(() => (rafId = requestAnimationFrame(resize)), 16)
    }

    const observer = new ResizeObserver(debounceResize)
    observer.observe(wrapperRef.current)
    observer.observe(mainBoardRef.current)

    resize()

    return () => {
      observer.disconnect()
      if (timeoutId) clearTimeout(timeoutId)
      if (rafId) cancelAnimationFrame(rafId)
    }
  }, [wrapperRef, mainBoardRef, lightRefs, loadingStage])
}
