import { useEffect } from 'react'

import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { useTimerStore } from '@repo/ui/store/useTimerStore'

import * as workerTimers from 'worker-timers'

const FRAME_INTERVAL = 1000 / 60 // ~16.67ms for 60fps

export default function useTicker() {
  const refresh = useTimerStore((state) => state.refresh)
  const { isIdleTimeout, isExpired, isMultiSession } = useNetworkStore((state) => ({
    isIdleTimeout: state.isIdleTimeout,
    isExpired: state.isExpired,
    isMultiSession: state.isMultiSession,
  }))

  useEffect(() => {
    if (isIdleTimeout || isExpired || isMultiSession) return

    const intervalId = simulateRAF(refresh)
    useTimerStore.setState({ timerId: intervalId })

    return () => {
      workerTimers.clearInterval(intervalId)
    }
    // TODO: Fixing this warning directly may break some functionality. This will require future refactoring.
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isIdleTimeout, isExpired, isMultiSession])
}

/**
 * Simulates requestAnimationFrame using workerTimers.setInterval.
 * Calls the callback every FRAME_INTERVAL ms with the current timestamp.
 */
function simulateRAF(callback: (timestamp: number) => void): number {
  return workerTimers.setInterval(() => {
    const now = Date.now()
    callback(now)
  }, FRAME_INTERVAL)
}
