import { useAutoPlayStore } from '@repo/ui/store/useAutoPlayStore'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { playSound, setMute } from '@repo/utils/common/sound'
import { connectIns } from '@repo/utils/socket'
import { useIdle } from '@uidotdev/usehooks'
import { useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import CenterModal from './CenterModal'

const IDLE_TIMEOUT = 10 * 60 * 1000 // 10 min
const FINAL_TIMEOUT = 5 * 60 * 1000 // 5 min

type ModalState = '' | 'idle' | 'timeout'

export default function IdleModal() {
  const isAutoPlay = useAutoPlayStore((s) => s.isAutoPlay)
  const [modalState, setModalState] = useState<ModalState>('')
  const [isInIdle, setIsInIdle] = useState(false)
  const timerId = useRef<NodeJS.Timeout | null>(null)

  const { t } = useTranslation()

  const idle = useIdle(IDLE_TIMEOUT)

  const forceShowIdleModal = useNetworkStore((state) => state.forceShowIdleModal)

  const handleResume = () => {
    playSound(['click'])
    setModalState('')
    setIsInIdle(false)
    if (timerId.current) {
      clearTimeout(timerId.current)
    }
  }

  const handleRefresh = () => {
    playSound(['click'])
    location.reload()
  }

  useEffect(() => {
    if (idle && !isAutoPlay && !isInIdle) {
      setModalState('idle')
      setIsInIdle(true)
    }
  }, [idle, isAutoPlay, isInIdle])

  useEffect(() => {
    if (modalState === 'idle') {
      timerId.current = setTimeout(() => {
        setModalState('timeout')
        setMute(true)
        connectIns?.close()
        useNetworkStore.setState({ isIdleTimeout: true })
      }, FINAL_TIMEOUT)

      return () => {
        if (timerId.current) {
          clearTimeout(timerId.current)
        }
      }
    }
  }, [modalState])

  return (
    <CenterModal
      open={!!modalState || forceShowIdleModal}
      icon="idle"
      text={t('COMMON.INACTIVITY')}
      confirmText={t(modalState === 'idle' ? 'COMMON.RESUME_PLAY' : 'COMMON.REFRESH')}
      onConfirm={modalState === 'idle' ? handleResume : handleRefresh}
    />
  )
}
