import { useLayoutStore } from '@repo/ui/store/useLayoutStore'
import { isMobileDevice } from '@repo/utils/common/common'
import { TAP_AREA_PX, TAP_COUNT, TAP_TIMEOUT_MS } from '@repo/utils/constants/debuggerConfig'

import { useEffect, useRef } from 'react'

interface UseDebuggerTriggerProps {
  onOpen: () => void
  keySequence: string
}

export default function useDebuggerTrigger({ onOpen, keySequence }: UseDebuggerTriggerProps) {
  const tapCountRef = useRef(0)
  const tapTimeoutRef = useRef<ReturnType<typeof setTimeout> | null>(null)
  const keySequenceRef = useRef('')

  /* -------------------------------------------------------------------------- */
  /*                                   Mobile                                   */
  /* -------------------------------------------------------------------------- */
  useEffect(() => {
    if (!isMobileDevice) return

    const onTouchEnd = (event: TouchEvent) => {
      const factor = useLayoutStore.getState().rootEmFontSize / 16
      const touch = event.changedTouches[0]
      const isTrigger = touch.clientX < TAP_AREA_PX * factor && touch.clientY < TAP_AREA_PX * factor
      if (!isTrigger) return

      // Increment tap count
      tapCountRef.current += 1

      // Start the timeout on the very first tap
      if (tapCountRef.current === 1) {
        tapTimeoutRef.current = setTimeout(() => {
          // Timeâ€™s up: reset count and timer
          tapCountRef.current = 0
          tapTimeoutRef.current = null
        }, TAP_TIMEOUT_MS)
      }
      // If we've hit the threshold, open debugger
      if (tapTimeoutRef.current && tapCountRef.current >= TAP_COUNT) {
        clearTimeout(tapTimeoutRef.current)
        tapTimeoutRef.current = null
        tapCountRef.current = 0
        onOpen()
      }
    }

    document.addEventListener('touchend', onTouchEnd)

    return () => {
      document.removeEventListener('touchend', onTouchEnd)
      if (tapTimeoutRef.current) clearTimeout(tapTimeoutRef.current)
    }
  }, [onOpen])

  /* -------------------------------------------------------------------------- */
  /*                                   Desktop                                  */
  /* -------------------------------------------------------------------------- */
  useEffect(() => {
    if (isMobileDevice) return

    const onKeyDown = (event: KeyboardEvent) => {
      const target = event.target as HTMLElement
      const shouldIgnore = target.isContentEditable || ['INPUT', 'TEXTAREA', 'SELECT'].includes(target.tagName)

      if (shouldIgnore) return

      if ((event as unknown as { isComposing: boolean }).isComposing) return

      // Match physical digit keys (0â€“9) regardless of layout/language
      if (/^Digit\d$/.test(event.code)) {
        const digit = event.code.slice(-1) // "Digit5" â†’ "5"
        keySequenceRef.current = (keySequenceRef.current + digit).slice(-keySequence.length)
        if (keySequenceRef.current === keySequence) {
          onOpen()
        }
      }
    }

    document.addEventListener('keydown', onKeyDown)
    return () => document.removeEventListener('keydown', onKeyDown)
  }, [keySequence, onOpen])
}
