import { lazy } from 'react'

import { isMobileDevice, isPrdEnv } from '@repo/utils/common/common'

import useGlobalOverlayHeightCalculator from '@repo/utils/hook/useGlobalOverlayHeightCalculator'
import useSafariCheck from '@repo/utils/hook/useSafariCheck'
import useTicker from '@repo/utils/hook/useTicker'
import { useSound } from '@roulette/hook/useSound'

import AutoPlayBetChangedModal from '@repo/ui/components/common/modal/AutoPlayBetChangedModal'
import AutoPlayFinishedModal from '@repo/ui/components/common/modal/AutoPlayFinishedModal'
import ExpiredModal from '@repo/ui/components/common/modal/ExpiredModal'
import GlobalModalOverlay from '@repo/ui/components/common/modal/GlobalModalOverlay'
import IdleModal from '@repo/ui/components/common/modal/IdleModal'
import MaintenanceModal from '@repo/ui/components/common/modal/MaintenanceModal'
import MultiSessionModal from '@repo/ui/components/common/modal/MultiSessionModal'
import Debugger from '@repo/ui/components/debug/Debugger'
import TooltipList from '@repo/ui/components/react-tooltip/TooltipList'
import TrafficLight from '@repo/ui/components/trafficLight/TrafficLight'

import blurBg from '@roulette/assets/mobile_blurBg.webp'

const MobileLayout = lazy(() => import('./layouts/mobile/MobileLayout'))
const DesktopLayout = lazy(() => import('./layouts/desktop/DesktopLayout'))

export default function App() {
  useSound()
  useSafariCheck()
  useTicker()
  useGlobalOverlayHeightCalculator()

  return (
    <div
      id="App"
      className="relative transition-opacity duration-[2s]"
      style={{
        backgroundImage: `url(${isMobileDevice && blurBg})`,
        backgroundSize: 'cover',
      }}
    >
      {isMobileDevice ? <MobileLayout /> : <DesktopLayout />}

      <TooltipList />

      <IdleModal />
      <ExpiredModal />
      <MultiSessionModal />
      <MaintenanceModal />

      <AutoPlayBetChangedModal />
      <AutoPlayFinishedModal />
      <GlobalModalOverlay />

      <TrafficLight />
      {!isPrdEnv && <Debugger />}
    </div>
  )
}
