import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isMobileDevice } from '@repo/utils/common/common'
const EventsLogDisplay = ({ isVisible }: { isVisible: boolean }) => {
  const sessionId = useConfigStore((s) => s.SESSION_ID)

  const { gameCode, roundId } = useConfigStore((s) => s.roundInfo)
  return (
    isVisible && (
      <div
        className={cn(
          'center pointer-events-none fixed left-1/2 top-1/2 z-[999] flex w-full -translate-x-1/2 -translate-y-1/2 flex-col bg-gray-900 bg-opacity-50 py-2',
          { 'text-xs': isMobileDevice }
        )}
      >
        <div>Session ID: {sessionId}</div>
        <div>Round ID: {roundId}</div>
        <div>Game Code: {gameCode}</div>
      </div>
    )
  )
}

export default EventsLogDisplay
