export type KeyFrame = {
  sec: number
  value: number
}

export function initGreenKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 10 * fps, value: 1 },
    { sec: 20 * fps, value: 0.8 },
    { sec: 30 * fps, value: 1 },
    { sec: 70 * fps, value: 0.5 },
    { sec: 85 * fps, value: 1 },
    { sec: 100 * fps, value: 1 },
    { sec: 160 * fps, value: 1 },
    { sec: 170 * fps, value: 0.8 },
    { sec: 180 * fps, value: 1 },
    { sec: 220 * fps, value: 0.5 },
    { sec: 235 * fps, value: 1 },
    { sec: 250 * fps, value: 1 },
    { sec: 300 * fps, value: 1 },
    { sec: 310 * fps, value: 0.8 },
    { sec: 320 * fps, value: 1 },
    { sec: 360 * fps, value: 0.5 },
    { sec: 375 * fps, value: 1 },
    { sec: 390 * fps, value: 1 },
    { sec: 420 * fps, value: 0 },
  ]
}

export function initYellowKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 390 * fps, value: 0 },
    { sec: 420 * fps, value: 1 },
    { sec: 510 * fps, value: 1 },
    { sec: 540 * fps, value: 0 },
  ]
}

export function initRedKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 510 * fps, value: 0 },
    { sec: 540 * fps, value: 1 },
    { sec: 600 * fps, value: 1 },
    { sec: 630 * fps, value: 1 },
    { sec: 660 * fps, value: 1 },
    { sec: 690 * fps, value: 0 },
  ]
}

export function initNormalKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 660 * fps, value: 0 },
    { sec: 690 * fps, value: 1 },
  ]
}

export function initWinKeyFrame(fps: number): KeyFrame[] {
  return [
    { sec: 0, value: 0 },
    { sec: 20 * fps, value: 1 },
    { sec: 40 * fps, value: 0 },
    { sec: 60 * fps, value: 1 },
    { sec: 80 * fps, value: 0 },
  ]
}
