import { isMobileDevice } from '@repo/utils/common/common'
import { useEffect, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { ChildrenType, IPosition, PlacesType, Tooltip, TooltipRefProps } from 'react-tooltip'
import { tooltipEventBus } from '../../store/tooltipEvent'

export interface TooltipImperativeOpenOptions {
  anchorSelect?: string
  position?: IPosition
  place?: PlacesType
  content?: ChildrenType
  delay?: number
}

export default function TooltipList() {
  const tooltipRef = useRef<TooltipRefProps>(null)
  const { t } = useTranslation()

  const openTooltip = (props: TooltipImperativeOpenOptions) => {
    return (extraProps?: TooltipImperativeOpenOptions) => {
      tooltipRef.current?.open({ ...props, ...extraProps })
      tooltipRef.current?.close({ delay: 3000 })
    }
  }

  // the anchorSelect must be in Mobile and Desktop layout
  useEffect(() => {
    tooltipEventBus.subscribe(
      'lowBalance',
      openTooltip({
        anchorSelect: '#MainBalance',
        content: t('COMMON.LOW_BALANCE1'),
        place: 'bottom-start',
      })
    )

    tooltipEventBus.subscribe(
      'betAutomatically',
      openTooltip({
        anchorSelect: '#AutoPlayBtn',
        content: t('COMMON.REPEAT_AUTOMATICALLY'),
        place: 'bottom-start',
      })
    )

    tooltipEventBus.subscribe(
      'empty',
      openTooltip({
        anchorSelect: '#MainUndoBtn',
        content: t('COMMON.FIRST_BETS'),
        place: 'top-start',
      })
    )

    tooltipEventBus.subscribe(
      'close',
      openTooltip({
        content: t('COMMON.NEXT_GAME_SOON'),
        place: 'top-end',
      })
    )

    tooltipEventBus.subscribe(
      'comingSoon',
      openTooltip({
        content: t('COMMON.COMING_SOON'),
        place: 'top-start',
      })
    )

    tooltipEventBus.subscribe(
      'overLimit',
      openTooltip({
        content: t('COMMON.MAX_LIMIT_REACHED'),
        place: 'top',
      })
    )

    tooltipEventBus.subscribe(
      'noBet',
      openTooltip({
        anchorSelect: '#AutoPlayBtn',
        content: t('COMMON.NO_BET'),
        place: isMobileDevice ? 'bottom-start' : 'top',
      })
    )

    tooltipEventBus.subscribe(
      'oppositeBet',
      openTooltip({
        content: t('COMMON.OPPOSITE_BETTING_RESTRICTED'),
        place: 'top',
      })
    )
  }, [t])

  const closeTooltip = () => {
    if (tooltipRef.current?.isOpen) {
      tooltipRef.current?.close()
    }
  }

  useEffect(() => {
    document.addEventListener('mousedown', closeTooltip)

    return () => document.removeEventListener('mousedown', closeTooltip)
  }, [])

  return (
    <Tooltip
      ref={tooltipRef}
      className="tooltipContainer"
      disableStyleInjection={true}
    ></Tooltip>
  )
}
