import { useDebugStore } from '@repo/ui/store/useDebugStore'
import { isInIframe } from '@repo/ui/utils/common'
import { cn, isMobileDevice } from '@repo/utils/common/common'
import { DEBUG_PANEL_KEY_SEQUENCE, LEVA_KEY_SEQUENCE, TAP_COUNT, TAP_TIMEOUT_MS } from '@repo/utils/constants/debuggerConfig'
import useKeyPress from '@repo/utils/hook/useKeyPress'
import { useDrag } from '@use-gesture/react'
import { memo, useEffect, useRef, useState } from 'react'
import IconMenu from '../../icons/IconMenu'
import { checkStorageQuota } from '../layout/loadResource-cache'
import './CustomDebugPanel.scss'

interface Props {
  isVisible: boolean
  onClose: () => void
}

function CustomDebugPanel({ isVisible, onClose }: Props) {
  const messages = useDebugStore((state) => state.msg)
  const debuggerRef = useRef<HTMLDivElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  const panelRef = useRef<HTMLDivElement>(null)
  const [isCollapsed, setIsCollapsed] = useState(false)

  /* -------------------------------------------------------------------------- */
  /*                              Drag functionality                            */
  /* -------------------------------------------------------------------------- */
  const [position, setPosition] = useState({ x: 0, y: 0 })
  const [isDragging, setIsDragging] = useState(false)

  const bind = useDrag(
    ({ offset: [x, y], dragging }) => {
      setPosition({ x, y })
      setIsDragging(!!dragging)
    },
    {
      filterTaps: true,
      pointer: { capture: false },
    }
  )!

  /* -------------------------------------------------------------------------- */
  /*                              Auto-scroll to bottom                         */
  /* -------------------------------------------------------------------------- */
  useEffect(() => {
    if (isVisible && containerRef.current) {
      containerRef.current.scrollTo({
        top: containerRef.current.scrollHeight,
        behavior: 'smooth',
      })
    }
  }, [isVisible, messages])

  /* -------------------------------------------------------------------------- */
  /*                              Storage Quota Info                            */
  /* -------------------------------------------------------------------------- */
  const [storageQuota, setStorageQuota] = useState<{
    usage: number
    quota: number
    ratio: number
  } | null>(null)

  useEffect(() => {
    if (isVisible) {
      checkStorageQuota().then(setStorageQuota)
    }
  }, [isVisible])

  /* -------------------------------------------------------------------------- */
  /*                                   HotKey: q                                */
  /* -------------------------------------------------------------------------- */
  const [errorToThrow, setErrorToThrow] = useState<Error | null>(null)

  if (errorToThrow) {
    throw errorToThrow // it can be caught by React ErrorBoundary
  }

  useKeyPress({
    targetKey: 'q',
    targetRef: debuggerRef,
    callback: () => {
      if (isVisible) {
        setErrorToThrow(new Error('kabooom!'))
      }
    },
  })

  /* -------------------------------------------------------------------------- */
  /*                                   HotKey: f                                */
  /* -------------------------------------------------------------------------- */
  useKeyPress({
    targetKey: 'f',
    targetRef: debuggerRef,
    callback: () => {
      if (isVisible && !isInIframe()) {
        const url = new URL(window.location.href).toString()
        window.open(`${window.location.origin}${window.location.pathname}iframe.html?url=${encodeURIComponent(url)}`, '_blank')
      }
    },
  })

  /* -------------------------------------------------------------------------- */
  /*                                   HotKey: i                                */
  /* -------------------------------------------------------------------------- */
  const [showIconMenu, setShowIconMenu] = useState(false)

  useKeyPress({
    targetKey: 'i',
    targetRef: debuggerRef,
    callback: () => {
      if (isVisible) {
        setShowIconMenu(true)
      }
    },
  })

  /* -------------------------------------------------------------------------- */
  /*                                   HotKey: Esc                              */
  /* -------------------------------------------------------------------------- */
  useKeyPress({
    targetKey: 'Escape',
    targetRef: debuggerRef,
    callback: () => {
      if (isVisible) {
        setShowIconMenu(false)
        onClose()
      }
    },
  })

  if (!isVisible) return null

  return (
    <div
      id="CustomDebugPanel"
      ref={debuggerRef}
      tabIndex={-1}
      className="fixed inset-0 z-[9999] flex items-center justify-center"
    >
      <div
        ref={panelRef}
        className={cn(
          'relative max-h-[90vh] overflow-y-auto rounded-2xl bg-white bg-opacity-90 p-4 shadow-2xl',
          isMobileDevice ? 'w-4/5' : 'w-1/4'
        )}
        style={{
          transform: `translate3d(${position.x}px, ${position.y}px, 0)`,
          userSelect: 'text',
        }}
      >
        <CloseButton onClose={onClose} />
        <h2
          {...bind()}
          className="mb-4 select-none text-2xl font-bold text-black"
          style={{
            cursor: isDragging ? 'grabbing' : 'grab',
            touchAction: 'none',
          }}
        >
          CustomDebugPanel
        </h2>

        <div
          ref={containerRef}
          className="mb-4 flex max-h-[50vh] w-full flex-col gap-2 overflow-auto py-1"
          style={{ userSelect: 'text' }}
        >
          {messages.length ? (
            messages.map(([time, msg], i) => (
              <Message
                key={`${time}-${i}`}
                timestamp={time}
                text={msg}
              />
            ))
          ) : (
            <Empty />
          )}
        </div>

        {/* Collapse Button */}
        <button
          onClick={() => setIsCollapsed(!isCollapsed)}
          className="mt-2 flex w-full items-center justify-between border-t-[0.0625rem] border-gray-200 py-2 text-xs text-gray-600 hover:bg-gray-50"
        >
          <span className="font-semibold">Additional Info</span>
          <span className="text-xs">{isCollapsed ? 'â–¼' : 'â–²'}</span>
        </button>

        {!isCollapsed && (
          <>
            {/* Storage Quota Section */}
            <div
              className={cn('border-t-[0.0625rem] border-gray-200 py-2 text-xs text-gray-600')}
              style={{ userSelect: 'text' }}
            >
              <h3 className="mb-2 text-sm font-semibold text-gray-800">Storage Quota</h3>
              {storageQuota ? (
                <div className="space-y-1 text-xs text-gray-600">
                  <div>Usage: {Math.round(storageQuota.usage / 1024 / 1024)}MB</div>
                  <div>Quota: {Math.round(storageQuota.quota / 1024 / 1024)}MB</div>
                  <div>Ratio: {Math.round(storageQuota.ratio * 100)}%</div>
                  <div className="mt-2">
                    <div className="h-2 w-full rounded-full bg-gray-200">
                      <div
                        className={cn(
                          'h-2 rounded-full transition-all',
                          storageQuota.ratio > 0.9 ? 'bg-red-500' : storageQuota.ratio > 0.7 ? 'bg-yellow-500' : 'bg-green-500'
                        )}
                        style={{ width: `${Math.min(storageQuota.ratio * 100, 100)}%` }}
                      />
                    </div>
                  </div>
                </div>
              ) : (
                <div className="text-xs text-gray-500">Storage API not supported</div>
              )}
            </div>

            <div
              className={cn('border-t-[0.0625rem] border-gray-200 pt-2 text-xs text-gray-600')}
              style={{ userSelect: 'text' }}
            >
              <span className="font-bold">Note:</span>
              <ol className="list-decimal">
                <li>
                  Press <kbd>i</kbd> to open the icon menu.
                </li>
                <li>
                  Press <kbd>q</kbd> to intentionally break the site.
                </li>
                <li>
                  Press <kbd>f</kbd> to open the game with third-party iframe.
                </li>
                {isMobileDevice ? (
                  <li>
                    Tap the top-left corner {TAP_COUNT} times within {TAP_TIMEOUT_MS}ms to open the debugger.
                  </li>
                ) : (
                  <>
                    <li>
                      Press <kbd>{DEBUG_PANEL_KEY_SEQUENCE}</kbd> to open the debugger.
                    </li>
                    <li>
                      Press <kbd>{LEVA_KEY_SEQUENCE}</kbd> to open the Leva Control Panel.
                    </li>
                    <li>
                      Press <kbd>Esc</kbd> to close.
                    </li>
                  </>
                )}
              </ol>
            </div>
          </>
        )}
      </div>
      {showIconMenu && <IconMenu />}
    </div>
  )
}

function Message({ timestamp, text }: { timestamp: string; text: string }) {
  return (
    <p className="whitespace-pre-line break-words text-xs text-gray-600">
      <span className="font-bold">{`[${timestamp}]`}</span> <br /> {text}
    </p>
  )
}

function Empty() {
  return <p className="text-center text-xs italic text-gray-400">No debug messages to display.</p>
}

function CloseButton({ onClose }: { onClose: () => void }) {
  return (
    <button
      aria-label="Close debugger"
      className="absolute right-3 top-3 aspect-square w-8 rounded-full p-2 text-gray-500 hover:bg-gray-200 hover:text-black focus:outline-none focus:ring-2 focus:ring-gray-400"
      onClick={onClose}
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 20 20"
      >
        <path
          stroke="currentColor"
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth="2"
          d="M6 6l8 8M14 6l-8 8"
        />
      </svg>
    </button>
  )
}

export default memo(CustomDebugPanel)
