import CenterModal from '@repo/ui/components/common/modal/CenterModal'
import { useNetworkStore } from '@repo/ui/store/useNetworkStore'
import { setMute } from '@repo/utils/common/sound'
import { useEffect } from 'react'
import { useTranslation } from 'react-i18next'

export default function MaintenanceModal() {
  const isMaintenance = useNetworkStore((state) => state.isMaintenance)
  const { t } = useTranslation()

  useEffect(() => {
    if (isMaintenance) return setMute(true)
  }, [isMaintenance])
  return (
    <CenterModal
      open={isMaintenance}
      icon={'maintenance'}
      text={t('COMMON.TABLE_MAINTENANCE')}
      textClassName="max-w-[12rem] whitespace-break-spaces"
      confirmText="Lobby"
    />
  )
}
