import { ONE_FRAME_TIME_SEC } from '@repo/ui/constants/animation'
import { useConfigStore } from '@repo/ui/store/useConfigStore'
import { cn, isDesktop } from '@repo/utils/common/common'
import { useRef } from 'react'
import { initGreenKeyFrame, initNormalKeyFrame, initRedKeyFrame, initWinKeyFrame, initYellowKeyFrame, KeyFrame } from './animation'

import useTrafficLightRender from './useTrafficLightRender'
import useTrafficLightResize from './useTrafficLightResize'

import './TrafficLight.scss'

export const TRAFFIC_LIGHT_COLOR = {
  GREEN: 'green',
  YELLOW: 'yellow',
  RED: 'red',
  NORMAL: 'normal',
  WIN: 'win',
} as const

export type TrafficLightColor = (typeof TRAFFIC_LIGHT_COLOR)[keyof typeof TRAFFIC_LIGHT_COLOR]

interface LightConfig {
  color: TrafficLightColor
  keyFrames: KeyFrame[]
}

interface TrafficLightFrameProps {
  color: TrafficLightColor
  lightRefs: React.RefObject<Map<TrafficLightColor, HTMLDivElement>>
}

export const LIGHT: LightConfig[] = [
  { color: TRAFFIC_LIGHT_COLOR.GREEN, keyFrames: initGreenKeyFrame(ONE_FRAME_TIME_SEC) },
  { color: TRAFFIC_LIGHT_COLOR.YELLOW, keyFrames: initYellowKeyFrame(ONE_FRAME_TIME_SEC) },
  { color: TRAFFIC_LIGHT_COLOR.RED, keyFrames: initRedKeyFrame(ONE_FRAME_TIME_SEC) },
  { color: TRAFFIC_LIGHT_COLOR.WIN, keyFrames: initWinKeyFrame(ONE_FRAME_TIME_SEC) },
  { color: TRAFFIC_LIGHT_COLOR.NORMAL, keyFrames: initNormalKeyFrame(ONE_FRAME_TIME_SEC) },
]

function TrafficLight() {
  const isCompact = useConfigStore((s) => s.IS_SMALL_DEVICE)
  const wrapperRef = useRef<HTMLDivElement>(null)
  const mainBoardRef = useRef<HTMLDivElement>(null)
  const lightRefs = useRef(new Map<TrafficLightColor, HTMLDivElement>())

  useTrafficLightResize(wrapperRef, mainBoardRef, lightRefs)
  useTrafficLightRender(lightRefs)

  return (
    <div
      id="TrafficLight"
      ref={wrapperRef}
      className={cn('pointer-events-none absolute left-0 top-0 size-full', {
        isCompact,
        'z-[11]': isDesktop,
        'z-[2]': !isDesktop,
      })}
    >
      {LIGHT.map(({ color }) => (
        <TrafficLightFrame
          key={color}
          color={color}
          lightRefs={lightRefs}
        />
      ))}
    </div>
  )
}

function TrafficLightFrame({ color, lightRefs }: TrafficLightFrameProps) {
  return (
    <div
      ref={(el) => {
        if (el) lightRefs.current.set(color, el)
      }}
      className={cn('trafficLightWrapper', color)}
    >
      <div className={cn('trafficLightFrame', color)}></div>
    </div>
  )
}

export default TrafficLight
