import { useEffect } from 'react'

import { getWinAnimStore } from '@repo/ui/store/useWinAnimStore'
import commonSoundResources from '@repo/ui/utils/resources/commonSounds'
import { playSound } from '@repo/utils/common/sound'
import { useBaseSound } from '@repo/utils/hook/useSound'

import rouletteSoundResources from '@roulette/utils/resources/rouletteSounds'

const useWinAnimStore = getWinAnimStore<string>()

export const useSound = () => {
  useBaseSound({ ...rouletteSoundResources, ...commonSoundResources })

  const animState = useWinAnimStore((s) => s.animState)
  const winResult = useWinAnimStore((s) => s.winResult)

  useEffect(() => {
    if (animState !== 'animationStart' || !winResult) return
    const { result, userWin } = winResult
    const list: string[] = [result.toString()]
    if (userWin > 0) {
      setTimeout(() => playSound(['winEffect']), 600)
      list.push('youWin')
    }
    playSound(list)
  }, [animState, winResult])
}
